import { ajax } from '../../common/js/util.js'
export default {
	data() {
		return {
			name: '',
			head: '',
			tel: '',
			call: '',
			scores: 0
		}
	},
	onLoad() {
		this.call = this.$store.state.customer
	},
	onReady() {
	
	},
	onShow() {
		//刷新保留用户uid
		if (this.getRequest('uid')) {
			var uids = this.getRequest('uid')
			this.$store.commit('changeUid', uids)
		}
		this.init()
	},
	methods: {
		getRequest(variable) {
			var query = window.location.search.substring(1);
			var vars = query.split("&");
			for (var i = 0; i < vars.length; i++) {
				var pair = vars[i].split("=");
				if (pair[0] == variable) {
					return pair[1];
				}
			}
			return (false);
		},
		init() {
			let self = this
			let datas = {
				uid: this.$store.state.uid
			}
			let data = {
				url: '/api/gzh/userInfo',
				data: datas,
				success: function(res){
					console.log(res)
					if(res.data.result == 0) {
						self.head = res.data.icon
						self.name = res.data.username
						self.tel = res.data.phone
						self.scores = res.data.point
					}
				}
			}
			ajax(data)
		},
		goScore() {
			uni.navigateTo({
				url: '../score/score?scores=' + this.scores
			})
		},
		goOrder() {
			uni.navigateTo({
				url: '../order/order'
			})
		},
		editor() {
			uni.navigateTo({
				url: '../editor/editor'
			})
		},
		choose() {
			uni.chooseImage({
				success: (chooseImageRes) => {
					const tempFilePaths = chooseImageRes.tempFilePaths;
					this.url = tempFilePaths[0]
					console.log(tempFilePaths[0])
					// uni.uploadFile({
					// 	url: 'https://www.example.com/upload', //仅为示例，非真实的接口地址
					// 	filePath: tempFilePaths[0],
					// 	name: 'file',
					// 	formData: {
					// 		'user': 'test'
					// 	},
					// 	success: (uploadFileRes) => {
					// 		console.log(uploadFileRes.data);
					// 	}
					// });
				}
			});
		},
		goFriends() {
			uni.navigateTo({
				url: '../friends/friends'
			})
		},
		set() {
			uni.navigateTo({
				url: '../set/set'
			})
		},
		goUs() {
			uni.navigateTo({
				url: '../content/content?url=' + encodeURIComponent('http://m.xgcyz1978.com/display/agreement?id=1')
			})
		},
		goNo(tit) {
			uni.navigateTo({
				url: '../noOpen/noOpen?title=' + tit
			})
		},
		goCall() {
			let self = this
			uni.makePhoneCall({
				phoneNumber: self.call
			})
		}
	}
}
